.navigation-items {
  display: flex;
  flex-direction: row;
  gap: 20px;
}
.navigation-items > ul {
  font-size: 20px;
  display: flex;
  gap: 20px;
}
@media only screen and (min-width: 1920px) {
  .navigation-items > ul {
    font-size: 24px;
  }
}
@media only screen and (max-width: 768px) {
  .navigation-items {
    position: absolute;
    background-color: #f5f5f5;
    right: 0;
    top: 0;
    width: 100%;
    max-width: 0;
    transition: all 0.5s ease-in-out 0.2s;
    flex-direction: column;
    z-index: 9999;
    padding: 24px 0;
    opacity: 0;
    min-height: 100dvh;
  }
  .navigation-items > ul {
    padding-top: 50px;
    font-size: 0;
    flex-direction: column;
    text-align: left;
  }
  .navigation-items--opened {
    max-width: 300px;
    padding-inline: 20px;
    opacity: 1;
  }
  .navigation-items--opened > ul {
    font-size: 24px;
  }
  .navigation-link {
    font-weight: 600;
  }
}
@media only screen and (max-width: 500px) {
  .navigation-items > ul {
    text-align: center;
  }
  .navigation-items--opened {
    max-width: 100%;
  }
}
